<template>
  <v-layout mt-5 wrap>
    <v-flex v-for="i in portfolios.length > limits ? limits : portfolios.length" md3 sm6 xs12>
      <Portfolio class="ma-3 word-boundary"
              :id="i"
              :date="portfolios[i - 1].portDatetime"
              :title="portfolios[i - 1].portTitle"
              :body="changeBody(portfolios[i - 1].portContent)"
              :imgSrc="portfolios[i - 1].portImg"
      ></Portfolio>
    </v-flex>

    <v-flex xs12 text-xs-center round my-5 v-if="loadMore">
      <v-btn color="rgba(182, 198, 207, 1)" dark v-on:click="loadMorePortfolios"><v-icon size="25" class="mr-2">fa-plus</v-icon>  </v-btn>
      <v-btn color="rgba(182, 198, 207, 1)" dark><router-link to="/portfolio/write" style="color:white; text-decoration:none;"><i class="fas fa-pen mr-2"></i>  </router-link></v-btn>
    </v-flex>
  </v-layout>
</template>
<script>
import Portfolio from '@/components/Portfolio'
import Post from '@/services/postController'
import axios from 'axios'

export default {
	name: 'PortfoliosList',
	props: {
		limits: {type: Number, default: 4},
    loadMore: {type: Boolean, default: false}
	},
	data() {
		return {
      portfolios: []
    };
	},
	components: {
		Portfolio
	},
	mounted() {
		this.getPortfolios()
	},

	methods: {
    async getPortfolios() {
      try{
        this.portfolios = await Post.getPortfolios();
        this.$store.state.portfolios = this.portfolios
      } catch(e){
        alert(e)
      }
    },
    loadMorePortfolios() {
      this.limits += 4
    },
    changeBody:function(text){
      return text.replace(/!\[\]\(https\:\/\/i.imgur.com\/[^]*.[^]*g\)/gi,"")
    }
  },
}
</script>
<style>
  .mw-700 {
    max-width: 700px;
    margin: auto;
  }
  .word-boundary{
    word-wrap: break-word;
  }
  .btn-color {

  }
</style>



