<template>
  <v-layout py-4 h-100 >

    <v-hover>
        <v-flex row flat class="txt-boundary" slot-scope="{ hover }" :class="`elevation-${hover ? 12 : 0}`">
            <div class="caption txt-title">{{date}}</div>
            <h2 class="color-333 headline font-weight-light txt-title">{{dtitle}}</h2>
            <p class="mb-1 color-666 font-weight-light subheading txt-limit">{{dbody}}</p>
            <!-- <div v-if="tran==0">
                <h2 class="color-333 headline font-weight-light txt-title">{{title}}</h2>
                <p class="mb-1 color-666 font-weight-light subheading txt-limit">{{body}}</p>
            </div> -->
            <!-- <div v-else>
                <h2 class="color-333 headline font-weight-light txt-title">{{transT}}</h2>
                <p class="mb-1 color-666 font-weight-light subheading txt-limit">{{transB}}</p>
            </div> -->
        </v-flex>
    </v-hover>


  </v-layout>
</template>

<script>
import Trans from "@/services/translate";
import AsyncComputed from 'vue-async-computed'

export default {
  name: 'Post',
    props: {
        date: {type: String},
        title: {type: String},
        body: {type: String},
        tran : {type: Number},
    },
    data(){
        return{
            dtitle : '',
            dbody : '',
        }
    },
    methods:{
        async translate(txt){
            try {
                return await Trans.translate(txt);
                // return await Trans.translate(txt);
            } catch (err) {
                alert(err)
            }
        },
        transT : async function(){
            this.dtitle = await this.translate(this.dtitle);
        },
        transB : async function(){
            this.dbody = await this.translate(this.dbody);
        },
    },
    watch:{
        'tran' : function(value, oldValue) {
            this.transT();
            this.transB();
        },
        immediate: true //    
    },
    mounted(){
        this.dtitle = this.title;
        this.dbody = this.body;
    }
}
</script>
<style>
  .color-666 {
    color: #666;
  }
  .color-333 {
    color: #333;
  }
  .h-100 {
    height: 100%;
  }

  .txt-boundary {
    /*           */
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-line-clamp: 1;
    padding: 7px;

    /* word-wrap: break-word; */
  }
  .txt-title{
    text-overflow: ellipsis;
    overflow: hidden;
  }
  .txt-limit {
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    word-break:break-all;
    -webkit-box-orient: vertical;
    /* word-wrap: break-word; */
    line-height: 1.2em;
    height: 4.8em;
    /* 4 4.8em 3 3.6em */
  }
  .txt-limit:hover {
    overflow: visible;
    text-overflow: visible;
    -webkit-line-clamp: none;
    word-break:none;
  }
</style>



