<template>
  <v-footer class="footer-color" absoulte height="auto">
 <v-card flat tile class="lighten-1 white--text text-xs-center wd-100 footer-color">
   <v-card-text>
     <v-btn v-for="link in links" class="mx-3 white--text" :href="link.url" target="_blank" icon>
       <v-icon size="24px">{{ link.icon }}</v-icon>
     </v-btn>
   </v-card-text>

   <!-- <v-flex class="text-xs-right">
            <table cellspacing="5px" class="text-xs-left">
                <tr>
                    <td rowspan="2"><i v-bind:class="weathericon" style="font-size:2em;"></i></td>
                    <td>  : {{ temperature }}&deg;C</td>
                    <td></td>
                    <td>  : {{ wind }}m/s</td>
                </tr>
                <tr>
                    <td>   : {{ humidity }}%</td>
                    <td></td>
                    <td>   : {{ cloud }}</td>
                </tr>
            </table>
        </v-flex> -->

   <v-divider></v-divider>

   <v-card-text class="white--text text-xs-center" style="vertical-align:middle;font-size:2vh;">
     <div style="float:left; height:4.3em;">
       <i v-bind:class="weathericon" style="font-size:2vh;">{{ temperature }}&deg;C</i>
     </div style="font-size:2vh;">
           &copy;2019 <strong>SSAFY 1st 7TEAM All Right Reserved.</strong>
           <br><strong>&nbsp;Production Period : 2019-07-08 ~ 2019-07-19</strong>

   </v-card-text>
   <router-link to="/logPage" style="font-size:1px; text-decoration:none; color:#b6c6cf;">logPage</router-link>
 </v-card>
</v-footer>

</template>

<script>
export default {
  name: 'Footer',
  data() {
    return {
      view: false,
      country: '',
      city: '',
      temperature:'',
      humidity:'',
      weather:'',
      description:'',
      weathericon:'',
      wind:'',
      cloud:'',
      links:[
        {
          icon:'fab fa-facebook',
          url:'https://www.facebook.com'
        },
        {
          icon:'fab fa-twitter',
          url:'https://www.twitter.com'
        },
        {
          icon:'fab fa-google-plus',
          url:'https://www.google.com'
        },
        {
          icon:'fab fa-linkedin',
          url:'https://www.linkedin.com'
        },
        {
          icon:'fab fa-instagram',
          url:'https://www.instagram.com'
        },
        {
          icon: 'fab fa-gitlab',
          url:'https://lab.ssafy.com/emino39/webmobile-sub2'
        }
      ]
    }
  },
  computed: {
      hasResult: function() {
        return this.posts.length > 0
      }
  },
  mounted() {


    this.searchWeather()
  },
  methods: {
    searchWeather() {
      const BASE_URL = 'http://api.openweathermap.org/data/2.5/weather?q=Seoul&appid=03b171cba590817b47d1ccc746057274'
      var dict = {
      '01d': 'wi-day-sunny',
      '02d': 'wi-day-cloudy',
      '03d': 'wi-cloud',
      '04d': 'wi-cloudy',
      '09d': 'wi-showers',
      '10d': 'wi-day-rain-mix',
      '11d': 'wi-thunderstorm',
      '13d': 'wi-snow',
      '50d': 'wi-fog',
      '01n': 'wi-night-clear',
      '02n': 'wi-night-alt-cloudy',
      '03n': 'wi-night-alt-cloudy-high',
      '04n': 'wi-cloudy',
      '09n': 'wi-night-alt-sprinkle',
      '10n': 'wi-night-alt-showers',
      '11n': 'wi-night-alt-thunderstorm',
      '13n': 'wi-night-alt-snow',
      '50n': 'wi-night-fog'
    };
      this.$http.get(`${BASE_URL}`)
      .then((result) => {
        this.country = result.data.sys.country
        this.city = result.data.name
        this.temperature=(result.data.main.temp - 273.15).toFixed(1)
        this.humidity=result.data.main.humidity
        this.weather = result.data.weather[0].main
        this.description = result.data.weather[0].description
        this.weathericon = "wi "+dict[result.data.weather[0].icon]
        // this.weathericon = "http://openweathermap.org/img/w/" + result.data.weather[0].icon + ".png"
        this.wind = result.data.wind.speed
        this.cloud= result.data.clouds.all+"%"
        // this.view = true
      })
    }
  }
}
</script>

<style scoped>
.wd-100{
  width:100%;
}
.ml-auto{
  margin-left:auto;
}
.footer-color{
  background: #b6c6cf;
  /* background: linear-gradient(
    to right,
    #e730c9,
    #f34865
  ); */
}
.mx-2{
  margin-left: 2px;
  margin-right:2px;
}
footer {
  position: relative;
  bottom: 0;
  width: 100%;
  padding: 0px 20px;
  height: 80px;
  text-align: left;
  font-size:0.7em;
  box-sizing: border-box;
  /* background: #6bbcdc; */
  background: #ffffff;
  /* background: -webkit-linear-gradient(
    to right,
    #e730c9,
    #f34865
  );
  background: linear-gradient(
    to right,
    #e730c9,
    #f34865
  );*/
  z-index:4;
}

</style>



