<template>
  <div>
    <ImgBanner :imgSrc="this.$store.state.imagebannersrc">
      <span style="line-height:1.2em;font-size:1.2em;" slot="text">Portfolio</span>
    </ImgBanner>
    <v-container>

      <!-- Portfolio -->
      <v-layout>
        <v-flex xs12>
          <PortfolioList :limits="6" :load-more="true"></PortfolioList>
        </v-flex>
      </v-layout>

    </v-container>


  </div>
</template>

<script>
import ImgBanner from '../components/ImgBanner'
import PortfolioList from '../components/PortfolioList'


export default {
	name: 'PortfolioPage',
	components: {
		ImgBanner,
		PortfolioList
	},
}
</script>



