<template>
  <v-hover>
    <!--      -->
  <v-card :to="{name: 'detail', params: {id: id}}" slot-scope="{ hover }" :class="`elevation-${hover ? 12 : 0}`">
<!--      -->
  <!-- <v-card slot-scope="{ hover }" :class="`elevation-${hover ? 12 : 0}`"> -->

    <v-img :src="imgSrc" height="200px">
    </v-img>
    <v-card-title primary-title>
      <div class="txt-boundary">
        <div class="headline txt-title">{{title}}</div>
        <span class="grey--text txt-limit">{{body}}</span>
<!--      -->
        <!-- <FullModal
        :id="id"
        :date="date"
        :title="title"
        :body="body"
        :imgSrc="imgSrc"></FullModal> -->
      </div>
    </v-card-title>
  </v-card>
</v-hover>


</template>

<script>
import FullModal from '../components/FullModal'
export default {
	name: 'Portfolio',
  components:{
    FullModal
  },
	props: {
    id: { type: Number },
		date: {type: String},
		title: {type: String},
		body: {type: String},
		imgSrc: {type: String},
	},
	data() {
		return {
			portfolio:{
        title:"{{title}}",
      }
		}
	},
 //  mounted(){
 //   this.changeBody(this.body)
 // },
 methods:{
   changeBody:function(text){
     this.body = text.replace(/!\[\]\(https\:\/\/i.imgur.com\/[^]*.[^]*g\)/gi,"")
   }
 }
}
</script>


<style>
.txt-boundary {
  /*           */
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-line-clamp: 1;


  /* word-wrap: break-word; */
}
.txt-title{
  text-overflow: ellipsis;
  overflow: hidden;
}
.txt-limit {
  white-space: normal;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  word-break:break-all;
  -webkit-box-orient: vertical;
  /* word-wrap: break-word; */
  line-height: 1.2em;
  height: 4.8em;
  /* 4 4.8em 3 3.6em */
}
</style>



