<script>
  //Importing Line class from the vue-chartjs wrapper
  import {Line} from 'vue-chartjs'
  //Exporting this so it can be used in other components
  export default {
    extends: Line,
    props:{
      // labelss:{type: Number},
      // datas:{type:Number},
      headers:{type:String},
      base_url:{type:String},

    },
    data () {
      return {
        datacollection: {
          //X   ?
          //  
          labels: [],

          datasets: [
            {
              label: 'number of commit for a week',
              // backgroundColor: '#f87979',
              backgroundColor: 'white',
              pointBackgroundColor: 'white',
              borderWidth: 1,
              pointBorderColor: '#249EBF',
              //  
              //  
              data:[]
            }
          ]
        },
        //Chart.js options that controls the appearance of the chart
        options: {
          scales: {
            yAxes: [{
              ticks: {
                beginAtZero: true
              },
              gridLines: {
                display: true
              }
            }],
            xAxes: [ {
              gridLines: {
                display: false
              }
            }]
          },
          legend: {
            display: true
          },
          responsive: true,
          maintainAspectRatio: false
        },
        height:200
      }
    },
    methods:{
      getNumCommits(url,header){
      const BASE_URL = url

      this.$http.get(`${BASE_URL}`,{
        'headers':{'Authorization':header,}
      })
      .then((result) => {
        for(var i=0; i<52; i++) {
          this.datacollection.datasets[0].data.push(result.data.owner[i])
          this.datacollection.labels.push(52-i)
        }
        this.renderChart(this.datacollection, this.options)
      })
    }
    },
    mounted () {
      this.getNumCommits(this.base_url,this.headers)
    }
  }

</script>



