<template>
    <v-menu offset-y>
      <template v-slot:activator="{ on }" class="header-color">
        <v-btn flat v-on="on" style="color:white"></v-btn>
      </template>

      <v-list>
        <v-list-tile @click="getRandomImg">
          <v-list-tile-title style="text-align:center;"></v-list-tile-title>
        </v-list-tile>

        <v-list-tile>
          <div class="filebox">
            <label for="fileupload"></label>
            <input multiple accept="image/*" id="fileupload" name="file" ref="file" type="file" v-on:change="getFileImg">
          </div>
        </v-list-tile>
      </v-list>

    </v-menu>
</template>

<script>
  export default {
    data(){
      return{
      };
    },
    methods:{
      getRandomImg(){
        //this.$store.state.imagebannersrc="https://source.unsplash.com/random/1600x900"
        const BASE_URL = 'https://source.unsplash.com/random/1600x900'
        this.$http.get(`${BASE_URL}`)
        .then((result) => {
          if (result.status==200){
            // this.$store.state.imagebannersrc = result.request.responseURL
            localStorage.setItem("imagebannersrc",result.request.responseURL)
            this.$store.state.imagebannersrc=localStorage.getItem("imagebannersrc")

          }
        })
      },
      getFileImg(){
        const BASE_URL = 'https://api.imgur.com/3/image.json'
        const formData = new FormData();
        var image = document.getElementById('image');
        this.file = this.$refs.file.files[0];
        //console.log(this.file)
        //console.log(this.file)
        if(this.file){
          formData.append('image',this.file);
        }
        else{
          formData.append('image',this.imgSrc);
        }
        this.$http.post(`${BASE_URL}`,formData,
          {
            'headers':{'Authorization':'Client-ID 7126f20a233f2ae',}
          }
        )
        .then((result) => {
        //console.log(result.data.data)
        //console.log("..")
          if (result.status==200){
            localStorage.setItem("imagebannersrc",result.data.data.link)
            this.$store.state.imagebannersrc=localStorage.getItem("imagebannersrc")
          }
        })
      }


    }
  }
</script>


<style scoped>


.filebox label {
  display: inline-block;
  padding: .6em 1.13em;
  /* color: white; */
  font-size: inherit;
  line-height: normal;
  vertical-align: middle;
  cursor: pointer;
  /* border: 1px solid #ebebeb; */
  /* border-bottom-color: #e2e2e2; */
  /* border-radius: .25em; */
}
.filebox input[type="file"] { /*    */
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip:rect(0,0,0,0);
  border: 0;
}

.p-0{
  padding:0px;
  text-decoration-color: white;
}

.header-color {
  background: #b6c6cf;
}


</style>



