import axios from 'axios'

export default{
    getPortfolios(){
        return new Promise((resolve, reject) => {
            let url = 'http://localhost:8080/searchAllPortfolio.do';
            axios.get(url)
            .then(response => {
                resolve(response.data)
            })
            .catch(e => {
                alert("error" + e)
            })
        });
    },
    getPosts(){
        return new Promise((resolve, reject) => {
            let url = 'http://localhost:8080/searchAllPost.do';
            axios.get(url)
            .then(response => {
                resolve(response.data)
            })
            .catch(e => {
                alert("error" + e)
            })
        });
    }
}



