<template>
    <v-container>
    <v-layout align-center justify-center row fill-height>
        <v-flex xs12 text-xs-center>
            <v-layout align-center justify-center row fill-height elevation-5 style="min-height:500px;" white pa-4>
                <v-flex xs12 text-xs-center>
                    <div v-if="login">{{user}}, !</div>
                    <div v-else></div>
                    <login0 color="red"/>
                </v-flex>
            </v-layout>

        </v-flex>
    </v-layout>
    </v-container>
</template>

<script>
import login0 from "@/components/login0";

export default {
    name: 'LoginPage',
    data() {
      return {
          login : false,
          user : ''
      }
    },
    components: {
        login0,
    },
    methods: {
    },
    computed:{
        isLogin(){
            return this.$store.getters.getUser;
        },
      },
    mounted() {
      this.user = this.$store.state.user;
      if(this.user == '')
          this.login = false;
      else{
          this.login = true;
      }
    },
    watch:{
      isLogin(val, oldVal){
          this.user = val;
          if(val=='')
              this.login = false;
          else {
              this.login = true;
          }
      }
    }
}
</script>



