import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '../views/HomePage.vue'
import PostPage from '../views/PostPage.vue'
import PortfolioPage from '../views/PortfolioPage.vue'
import PortfoliowriterPage from '../views/PortfoliowriterPage.vue'
import DetailPortfolio from '../views/DetailPortfolio.vue'
import LoginPage from '../views/LoginPage.vue'
import logPage from '../views/logPage.vue'
import axios from 'axios'
import store from '../store'
import NotFound from '../views/NotFound.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            component: HomePage,
            beforeEnter: function(to, from, next){
                axios.get('http://localhost:8080/log.do',{
                    params:{
                        userId : store.state.user, //    !!
                        pageName : "HomePage"
                    }
                })
                next()
            }
        },
        {
            path: '/post',
            name: 'post',
            component: PostPage,

            beforeEnter: function(to, from, next){
                axios.get('http://localhost:8080/log.do', {
                    params:{
                        userId : store.state.user,
                        pageName : "PostPage"
                    }
                })
                next()
            }
        },
        {
            path: '/portfolio/write',
            name: 'portfoliowriter',
            component: PortfoliowriterPage,
            beforeEnter: function(to, from, next){
                axios.get('http://localhost:8080/log.do',{
                    params:{
                        userId : store.state.user,
                        pageName : "PortfoliowriterPage"
                    }
                })
                next()
            }
        },
        {
            path: '/portfolio',
            name: 'portfolio',
            component: PortfolioPage,
            beforeEnter: function(to, from, next){
                axios.get('http://localhost:8080/log.do',{
                    params:{
                        userId : store.state.user,
                        pageName : "PortfolioPage"
                    }
                })
                next()
            }
        },
        {
            path: '/portfolio/:id',
            name: 'detail',
            component: DetailPortfolio,
            beforeEnter: function(to, from, next){
                axios.get('http://localhost:8080/log.do',{
                    params:{
                        userId : store.state.user,
                        pageName : "DetailPortfolio"
                    }
                })
                next()
            }
          },
          {
            path: '/logPage',
            name: 'logPage',
            component: logPage
          },
          {
            path: '/login',
            name: 'login',
            component: LoginPage,
            beforeEnter: function(to, from, next){
                axios.get('http://localhost:8080/log.do',{
                    params:{
                        userId : store.state.user,
                        pageName : "LoginPage"
                    }
                })
                next()
            }
          },
          {
      			path: '*',
      			name: 'notfound',
      			component: NotFound
		       }
    ],
    scrollBehavior (to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition
        } else {
            return { x: 0, y: 0 }
        }
    }

})



