<template>
  <div>

    <ImgBanner :imgSrc="this.$store.state.imagebannersrc">
      <div style="line-height:1.2em;font-size:1.2em;" slot="text">Portfolio writer</div>
    </ImgBanner>
    <v-container>
        <div style="text-align:center; font-size:25px;">:
          <input type="text" v-model="portitle" style="border:black solid 1px;
                background-color:#e6f3ff" placeholder=""/></div><br>
       <v-layout row wrap>
      <v-flex md6>
        <h2>Markdown Preview</h2>
        <markdown-it-vue class="md-body" :content="content"/>

        <!-- <p :content="content"></p> -->
      </v-flex>

      <v-flex md6>
        <!-- <v-textarea solo name="portfoliowrite" v-model="content" @keyup.enter="submit"></v-textarea> -->

        <markdown-editor v-model="content" ref="markdownEditor"></markdown-editor>
        <ImgUploader
        :parent-message="parentMessage"
        @send-message="getChildMessage"
        ></ImgUploader>

        <v-flex class="d-flex justify-center">
          <v-btn
            color="rgba(182, 198, 207, 1)"
            class="white--text"
            depressed
            @click="submit">
            Submit
          </v-btn>
        </v-flex>
      </v-flex>
    </v-layout>
    </v-container>
  </div>
</template>

<script>
import MarkdownItVue from 'markdown-it-vue'
import 'markdown-it-vue/dist/markdown-it-vue.css'
import ImgBanner from '../components/ImgBanner'
import PortfolioList from '../components/PortfolioList'
import markdownEditor from 'vue-simplemde/src/markdown-editor'
import ImgUploader from '../components/ImgUploader'
import axios from 'axios'


export default {
	name: 'PortfoliowriterPage',
	components: {
		ImgBanner,
		PortfolioList,
    MarkdownItVue,
    markdownEditor,
    ImgUploader
	},
  data() {
    return {
      content: '# This is markdown editor!!',
      childMessage:'',
      firstimage:'http://dy.gnch.or.kr/img/no-image.jpg',
      parentMessage:'  ',
      portTitle: ''
    }
  },
    methods:{
      getChildMessage: function(text) {
        this.childMessage = text
        this.content += text
      },
      // getChildImage: function(image){
      //   if (!this.firstimage){
      //     this.firstimage = image
      //   }
      // },
      submit : function(content){
        //      .
        //console.log(";;")
        //console.log(this.portitle)
        if(this.portitle == null){
          this.portitle = ''
        }
        //console.log(this.content)
        //this.getChildImage(img)
        this.firstimage = this.content.match(/https\:\/\/i.imgur.com\/[^]*.[^]*g/)
        //console.log(this.firstimage)
        if (this.firstimage === null){
         this.firstimage = ["http://dy.gnch.or.kr/img/no-image.jpg"];
         //console.log(this.firstimage)
       }

        //    
        axios.post('http://localhost:8080/newPortfolio.do',{
            portContent:this.content,
            portImg:this.firstimage[0],
            portTitle:this.portitle
        })
        .then((res)=>{
          //console.log(res.status)
        if(res.status==200){
           console.log("!")
           return this.$router.push({name: 'portfolio'});
        }
        else{
           alert("   !")
        }
        })
        
      }
    },
    watch:{
      content:function(val){
        console.log(val)
      }
    }
}

</script>


<style>
  @import 'simplemde/dist/simplemde.min.css';
  .btn-color {
    background: linear-gradient(
      to right,
      rgb(255, 255, 0),
      rgb(252, 100, 73)
    );
  }

</style>



