import axios from 'axios'

export default{
    login(id, pw){
        return new Promise((resolve, reject) => {
            let url = 'http://localhost:8080/login.do';
            axios.post(url, {
                userId : id,
                userPass : pw
            })
            .then(response => {
                console.log('mid' + response.data)
                resolve(response.data)
            })
            .catch(e => {
                alert("error" + e)
            })
        });
    },
    register(id, pw, gitId){
      return new Promise((resolve, reject) => {
        let url = 'http://localhost:8080/addUser.do';
        axios.post(url, {
          userId : id,
          userPass : pw,
          githubId : gitId
        })
        .then(response => {
          console.log('REG start'+response.data)
          resolve(response.data)
        })
        .catch(e => {
          alert("error" + e)
        })
      });
    }
}



