<template>
  <v-layout row wrap mw-1400>
    <v-flex xs12 text-xs-right>
        <v-btn flat small fab @click="toEn();">en</v-btn>
        <v-btn flat small fab @click="toKo();">ko</v-btn>
    </v-flex>
    <v-flex v-for="post in posts" :class="'xs' + 12 / column" px-3  md3 sm6 xs12>
        <Post
        :date="post.postDate"
        :title="post.postTitle"
        :body="post.postContent"
        :tran="tran">
        </Post>
        <v-divider></v-divider>
    </v-flex>
  </v-layout>
</template>
<script>
import Post from '@/components/Post'
import PostC from "@/services/postController"
import Trans from "@/services/translate"


export default {
  name: 'PostList',
    props: {
        column: {type: Number, default: 1},
        limits: {type: Number, default: 4},
        loadMore: {type: Boolean, default: false}
    },
    data() {
        return {
            posts: [],
            tran : 0
        }
    },
    components: {
        Post
    },
    async mounted() {
        await this.getPosts();
    },
    methods: {
        async getPosts() {
            this.posts = await PostC.getPosts();
        },
        loadMorePosts() {
        },
        toKo(){
            this.$store.commit('toKo');
            this.tran = 1;
        },
        toEn(){
            this.$store.commit('toEn');
            this.tran = 2;
        },
        link(){
            console.log("hi");
        }
    }
}
</script>
<style>
  .mw-1400 {
    max-width: 1400px;
    margin: auto;
  }
  .btn-color {
    background: linear-gradient(
      to right,
      rgb(252, 174, 73),
      rgb(255, 230, 8)
    );
  }
</style>



