<template>
  <div>
    <!-- <ImgBanner imgSrc="https://source.unsplash.com/5brvJbR1Pn8/1600x900"> -->
    <ImgBanner :imgSrc="this.$store.state.imagebannersrc">
      <span style="line-height:1.2em; color:white;" id="news" slot="text">HOMePAGE</span>
    </ImgBanner>

    <v-container>
      <!-- About Me -->
      <v-layout my-5>
        <v-flex xs12 sm8 text-xs-center text-sm-left>
          <h2 class="headline mb-3">About Me</h2>
          <p class="mr-4">
            {{this.$store.state.counter}}
                  , SSAFY 7.<br/>
                         . <br>
                                   .<br>
                             ! !</p>

        </v-flex>
        <v-flex sm4>
          <v-img :src="getImgUrl('profile.png')" aspect-ratio="1.5"/>
        </v-flex>
      </v-layout>

      <!-- Portfolio -->
      <v-layout my-5>
        <v-flex xs12>
            <!-- <v-btn flat href="/portfolio" target="_blank" class="headline"> -->
              <!-- target    -->
            <h2 class="headline my-5 text-xs-center">
              <v-btn flat><router-link to="/portfolio" class="headline" style="text-decoration:none;color:black">Portfolio</router-link></v-btn>
            </h2>

          <PortfolioList></PortfolioList>
        </v-flex>
      </v-layout>

      <!-- Post -->
      <v-layout my-5>
        <v-flex xs12>
          <h2 class="headline my-5 text-xs-center">
            <v-btn flat><router-link to="/post" class="headline" style="text-decoration:none;color:black">Post</router-link></v-btn>
          </h2>
          <PostList :column="2"></PostList>
        </v-flex>
      </v-layout>


      <!-- Github -->
      <v-layout my-5>
        <v-flex xs12>
          <h2 class="headline my-5 text-xs-center">
            <p class="headline">Project</p>
          </h2>
          <RepositoryList></RepositoryList>
        </v-flex>
      </v-layout>



        <!-- Snackbar -->

        <v-snackbar v-model="snackbar" bottom multi-line right>
              .
          <v-btn color="pink" flat @click="snackbar = false">Close</v-btn>
        </v-snackbar>

    </v-container>
  </div>
</template>

<script>
import ImgBanner from '../components/ImgBanner'
//import BannerText from '../components/BannerText'
import PortfolioList from '../components/PortfolioList'
import PostList from '../components/PostList'
import RepositoryList from '../components/RepositoryList'
import PortfolioPage from './PortfolioPage.vue'

export default {
	name: 'HomePage',
  data() {
  return {
    snackbar: false
    };
  },
	components: {
		ImgBanner,
		PortfolioList,
		PostList,
		RepositoryList,
    PortfolioPage
	},
	methods: {
    getImgUrl(img) {
			return require('../assets/' + img)
	   }
  },
  created() {
    const title = document.title;
    const url = location.href;
    var triggerDefault = false;
    var isChromium = window.chrome,isOpera = window.navigator.userAgent.indexOf("OPR") > -1 || window.navigator.userAgent.indexOf("Opera") > -1;

    if (window.sidebar && window.sidebar.addPanel) {
      //firefox version<23
      console.log("firefox before 23")
      return (this.snackbar = true);
    }else if(isChromium !== null && isOpera == true) {
   // Opera (chromium)
    return (this.snackbar = true);
    }else if ((window.sidebar && (navigator.userAgent.toLowerCase().indexOf('firefox') > -1)) || (window.opera && window.print)) {
      // Firefox version >= 23 and Opera Hotlist
      return (this.snackbar = true);
      console.log("firefox or opera")
    } else if(window.external && ('AddFavorite' in window.external)){
      //IE
      console.log("IE")
      return (this.snackbar = true);
    } else if (document.all) {
      //IE
      return (this.snackbar = true);
    }



  }
}
</script>



