<template>
    <v-layout row justify-center>
        <div class="mask" @click="hide" :style="makeMask"></div>
        <!--     ,      -->
        <v-btn flat @click="wrapWindowByMask" :style="style" v-if="!user">Login</v-btn>
        <v-btn flat @click="logout" :style="style" v-else>Logout</v-btn>

        <div class="modal-wrapper">

            <!--  modal -->
            <div v-if="show" class="modal-container" :style="left">

                <form v-on:submit.prevent="sendPost">
                    <div class="modal-header">
                        <slot name="header">
                            Login
                        </slot>
                    </div>

                    <div class="modal-body">
                        <input v-model="id" placeholder="ID" style="width:90%" type="email" @keyup.enter="enter">
                        <input v-model="pw" placeholder="PW" type="password" style="width:90%" @keyup.enter="enter">
                    </div>

                    <div class="modal-footer">
                        <v-btn flat @click="sendPost" :disabled="can">
                            Login
                        </v-btn>
                        <v-btn depressed color='#1976D2' dark @click="loginWithFacebook">
                            Facebook Login
                        </v-btn>
                        <v-btn flat @click="showReg=true;show=false;">
                            Register
                        </v-btn>
                    </div>
                </form>
            </div>

            <!--  modal -->
            <div v-if="showReg" class="modal-containerReg" :style="left">

                <form v-on:submit.prevent="sendPostReg">
                    <div class="modal-header">
                        <slot name="header">
                            Register
                        </slot>
                    </div>

                    <div class="modal-body">
                        <input v-model="idreg" placeholder="ID" id="email" style="width:90%">
                        <input v-model="pwreg" placeholder="PW" type="password" style="width:90%">
                        <input v-model="pwchk" placeholder="PW check" type="password" style="width:90%">
                        <input v-model="github" placeholder="Github ID" style="width:90%">
                    </div>

                    <div class="modal-footer">
                        <v-btn flat @click="sendPostReg" :disabled="canReg">
                            Register
                        </v-btn>
                    </div>
                </form>
            </div>
        </div>
    </v-layout>

</template>


<script>
import 'expose-loader?$!expose-loader?jQuery!jquery'
import FirebaseService from "@/services/FirebaseService"
import User from "@/services/userController";
import axios from 'axios'

export default{
    props:{
        color : {type : String}
    },
    data() {
        return{
            id : '',
            pw : '',
            idreg : '',
            pwreg : '',
            pwchk : '',
            github : '',
            show : false,
            showReg : false,
            width : 0,
            height : 0,
            user : false,
        }
    },
    methods: {
        sendPost: async function () {
          const exptext = /^[A-Za-z0-9_\.\-]+@[A-Za-z0-9\-]+\.[A-Za-z0-9\-]+/;
          //  +@+.+  
          if(exptext.test(this.id)==false){
            alert("   !");
            return;
          }
            try{
                var res = await User.login(this.id,this.pw);
                if(res){
                    this.hide();
                    this.login();
                }
                else{
                    alert("  .")
                }
                this.id = this.pw = '';
            } catch(e){
                alert(e)
            }
        },
        sendPostReg: async function () {
            const exptext = /^[A-Za-z0-9_\.\-]+@[A-Za-z0-9\-]+\.[A-Za-z0-9\-]+/;
            //  +@+.+  
            if(exptext.test(this.idreg)==false){
              alert("   !");
              return;
            }
            if(this.pwreg != this.pwchk){
                alert("  .")
                return;
            }
            var res = await User.register(this.idreg, this.pwreg, this.github);
            if(res){
                alert(" ")
                this.show = true;
                this.showReg = false;
            }
            else{
                alert(" ")
            }

            this.idreg = '';
            this.pwreg = '';
            this.pwchk = '';
            this.github = '';
        },
        wrapWindowByMask(){
            //  -  1   80%  .
            //$('.mask').fadeIn(1000);
            $('.mask').fadeTo("slow",0.6);

            //   .
            this.show = true;
        },
        hide(){
            $('.mask').hide();
            //$('.modal-container').hide();
            this.show=false;
            this.showReg=false;
        },
        getWindowWidth(event) {
            this.width = document.documentElement.clientWidth;
        },

        getWindowHeight(event) {
            this.height = document.documentElement.clientHeight;
        },
        logout(){
          if (this.$store.state.accessToken !== '') {
            this.logoutFacebook()
            this.$store.commit('logout')
            this.user=false
          } else {
            this.$store.commit('logout');
            this.user=false;
          }
        },
        login(){
          console.log("login.vue:this.login: " + this.id)
            this.$store.commit('login',{id : this.id});
        },
        enter(){
            if(this.id!='' && this.pw!=''){
                this.sendPost();
            }
        },
        loginFb(email){
            this.$store.commit('login',{id : email});
        },
        async loginWithFacebook(){
          try {
            const result = await FirebaseService.loginWithFacebook()
            const email = result.user.email
            this.$store.state.accessToken = result.credential.accessToken
            this.$store.state.user = email
            this.show = false
            this.hide()
            alert('  .')
            this.loginFb(email)
          }
          catch(error) {
            alert('    .')
          }
        },
        async logoutFacebook() {
            try {
              await FirebaseService.logoutFacebook()
              alert('  .')
            }
            catch(error) {
              alert(' ')
            }
        }
    },
    computed:{
        can(){
            if(this.id == '' || this.pw == '')
            return true;
            return false;
        },
        canReg(){
            if(this.idreg=='' || this.pwreg=='' || this.pwchk=='' || this.github=='')
            return true;
            return false;
        },
        left(){
            return "left:" + (this.width/2 - 150) + "px";
        },
        makeMask(){
            //       .
            return 'width:' + this.width + 'px;\nheight:' + this.height + 'px;';
        },
        isLogin(){
            return this.$store.getters.getUser;
        },
        style(){
            return "height:100%; color:" + this.color + ";";
        }
    },
    mounted(){
        this.$nextTick(function() {
            window.addEventListener('resize', this.getWindowWidth);
            window.addEventListener('resize', this.getWindowHeight);

            this.getWindowWidth();
            this.getWindowHeight();
        })
        if(this.$store.getters.getUser =='')
        this.user = false;
        else {
            this.user = true;
        }
    },
    beforeDestroy() {
        window.removeEventListener('resize', this.getWindowWidth);
        window.removeEventListener('resize', this.getWindowHeight);
    },
    watch:{
        isLogin(val, oldVal){
            if(val=='')
              this.user = false;
            else {
              this.user = true;
            }
        }
    }
}
</script>

<style scoped>
.mask {
    position:absolute;
    z-index:9000;
    background-color:#000;
    display:none;
    left:0;
    top:0;
}

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
}

.modal-container {
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
    position:absolute;
    top:30%;
    z-index:10000;
}

.modal-containerReg {
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
    position:absolute;
    top:30%;
    z-index:10001;
}

.modal-header h3 {
    margin-top: 0;
    color: #42b983;
}

.modal-body {
    padding:20px 0;
}

.modal-footer{
    text-align: center;
    margin:auto;
    display:block;
}

.modal-default-button {
    float: right;
}

.modal-enter {
    opacity: 0;
}

.modal-leave-active {
    opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
}
</style>



