<template>
  <!-- <v-app v-resize-text> -->
  <v-app id="top_page">

    <!-- header -->
    <Header></Header>

    <v-content class="mt-5">

      <router-view/>
    </v-content>
    <!-- Footer -->
    <Footer></Footer>

    <!-- going to top button -->
    <v-btn style="z-index:5;"dark fab bottom right fixed color="rgba(0, 0, 0, 0.5)" v-show="scrolled" @click="$vuetify.goTo('#top_page')">
      <v-icon large>keyboard_arrow_up</v-icon>
    </v-btn>

  </v-app>
</template>

<script>
import 'es6-promise/auto'
import store from './store'
import Header from '@/components/Header.vue'
import Footer from '@/components/Footer.vue'

export default {
	name: 'App',
  data() {
    return {
      scrolled: false
    };
  },
	store,
  components: {
    Header,
    Footer
  },

  methods:{
    handleScroll() {
      this.scrolled = window.scrollY > 0;
    }
  },
  beforeMount() {
    document.addEventListener("scroll", this.handleScroll);
    console.log("scrolling Injected");
  },
  beforeDestroy() {
    document.removeEventListener("scroll", this.handleScroll);
    console.log("scrolling Destroyed");
  }

}




</script>
<!-- <style src='./assets/css/weather-icons-wind.min.css'></style> -->
<style>
#app{
  font-family : 'Avenir',Helvetica, Arial, sans-serif;
  font-size : 1rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* -webkit-text-size-adjust: auto; */
  /* text-align: center; */
  color: #2c3e50;
  /* margin-top: auto; */
  /* margin-bottom: 80px; */
}

@media (min-width: 24em) {
  :root {
    font-size: calc(1rem + ((1vw - 0.48rem) * 1.7889));
    /* Where: * 1.3889 = 100 * font_Size_Difference / viewport_Width_Difference */
  }
}

@media (min-width: 120em) {
  :root {
    font-size: 1em;
  }
}
.btn-color {
  background: #ffffff;
  /* background: linear-gradient(
    to right,
    rgb(255, 255, 0),
    rgb(252, 100, 73)
  ); */

}
</style>



