<template>
  <v-layout row justify-center>
    <v-dialog v-model="dialog" full-width hide-overlay transition="dialog-bottom-transition">
      <template v-slot:activator="{ on }">
        <v-btn class="header-color" dark v-on="on">More detail</v-btn>
      </template>

      <v-card @click="dialog = false">
        <v-toolbar class="header-color header-fix-top">
          <!-- <v-btn icon dark>
            <v-icon>close</v-icon>
          </v-btn> -->
          <v-toolbar-title>{{title}} / {{date}}</v-toolbar-title>
          <v-spacer></v-spacer>
          <!-- <v-toolbar-items>
            <v-btn dark flat @click="dialog = false">Save</v-btn>
          </v-toolbar-items> -->
        </v-toolbar>
        <v-responsive class="mt-5">
          <v-container fill-height>
            <v-layout align-center>
              <v-flex>
                <h2>{{title}}</h2>
                <p>{{date}}</p>
                <span class="grey--text">{{created_at}}</span>
                <v-divider class="my-3"></v-divider>
                <v-img aspect-ratio="1.7" contain :src="imgSrc"></v-img>
                <div class="my-3">{{body}}</div>
              </v-flex>
            </v-layout>
          </v-container>
        </v-responsive>

      </v-card>
    </v-dialog>
  </v-layout>
</template>



<script>
  export default {
    name:'FullModal',
    props: {
      id: { type: Number },
      date: {type: String},
      title: {type: String},
      body: {type: String},
      imgSrc: {type: String},
    },
    data () {
      return {
        dialog: false,
        notifications: false,
        sound: true,
        widgets: false
      }
    }
  }
</script>

<style>

.header-color {
  background: linear-gradient(
    to right,
    #e730c9,
    #f34865
  )
};
.header-fix-top{
  position: fixed;
  top: 0;
  /* width: 100%; */
  /* padding: 20px; */
  /* height: 80px; */
  box-sizing: border-box;
  /* background: #6bbcdc; */
};
.btn-color {
  background: linear-gradient(
    to right,
    rgb(255, 255, 0),
    rgb(252, 100, 73)
  );
};
</style>



