import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        accessToken: '',
        user: '',
        portfolios:[],
        source: 'en',
        target: 'ko',
        sessionId: '',
        imagebannersrc:localStorage.getItem("imagebannersrc")
    },
    mutations: {
        toEn(state){
            state.source = 'ko'
            state.target = 'en'
            //state.translate++
        },
        toKo(state){
            state.source = 'en'
            state.target = 'ko'
            //state.translate++
        },
        logout(state){
            state.accessToken = '';
            state.user = '';
            state.sessionId = '';
        },
        login(state,payload){
            state.user = payload.id;
            console.log("store:payload.id : " + payload.id)
            state.sessionId = payload.id;
        }
    },
    getters:{
        getSource: function(state){
            return state.source;
        },
        getTarget: function(state){
            return state.target;
        },
        getUser : function(state){
            return state.user;
        }
    },
    plugins: [new VuexPersistence({
        storage: window.sessionStorage,
        reducer: (state) => ({user: state.user}),
    }).plugin]
})

