<template>
  <v-layout column px-4>
    <!--    -->
    <v-layout row v-for="i in people.length" class = "hidden-sm-and-down-new">
      <v-flex >
        <v-divider></v-divider>
        <v-btn flat :href="people[i-1].repo_url">
          <h4>{{people[i-1].name}}</h4>
        </v-btn>
        <line-chart
        class ="hidden-sm-and-up-new"
        :height="150"
        :headers="people[i-1].header"
        :base_url="people[i-1].base_url"
        />
        <v-divider></v-divider>
      </v-flex>

    </v-layout>
    <!--   -->
    <!--<v-flex v-for="i in repositories.length > limits ? limits : repositories.length">
      <v-divider v-if="i === 1"></v-divider>
      <Repository :repos="repositories[i - 1]"></Repository>
      <v-divider></v-divider>
    </v-flex>-->


  </v-layout>

</template>

<script>
//import Repository from '@/components/Repository'
import VueCharts from 'vue-chartjs'
import { Bar } from 'vue-chartjs'
import LineChart from '@/components/LineChart'


export default {
	name: 'RepositoryList',
  extends:Bar,
	props: {
		limits: {type: Number, default: 5},
		loadMore: {type: Boolean, default: false}
	},
	data() {
		return {
      people:[
        {
        name:"Kim Donghwan / DongHwanmon / TIL",
        repo_url:"https://github.com/DongHwanmon/TIL",
        header:"token c6e48873107e09fd3353e14fec3b6f04806d9739",
        base_url:"https://api.github.com/repos/DongHwanmon/TIL/stats/participation"
      },
      {
        name:"Lee Jaewoo / Jaewoo-Lee / SSAFY_project",
        repo_url:"https://github.com/Jaewoo-Lee/SSAFY_project",
        header:"token 822c6954ccc7d843daccb2761bc191ade72c1da4",
        base_url:"https://api.github.com/repos/Jaewoo-Lee/SSAFY_project/stats/participation"
      },
      {
        name:"Lee Minyoung / emino39 / TIL",
        repo_url:"https://github.com/emino39/TIL",
        header:"token 825ed582d674374ba359b2f213ca07a046dff8c7",
        base_url:"https://api.github.com/repos/emino39/TIL/stats/participation"
      },
      {
        name:"Kim Youna / 0ul / Alg",
        repo_url:"https://github.com/0ul/Alg",
        header:"token e1383078e8b133f8952bdf5a7d71f446052e71cb",
        base_url:"https://api.github.com/repos/0ul/Alg/stats/participation"
      },
      {
        name:"Yang Heedong / Hdong27 / newB",
        repo_url:"https://github.com/Hdong27/newB",
        header:"token f8e37aca753835f324fe7be7ba77f6c6f13a39a4",
        base_url:"https://api.github.com/repos/Hdong27/newB/stats/participation"
      }],

      //repositories: []
    }
	},
	components: {
		//Repository,
    LineChart
	}
}
</script>

<style>
.max-height-200{
  max-height: 200px;
}
/* (600px)  */
@media only screen and (max-width: 600px) {
  .hidden-sm-and-down-new {
    display: none !important;
    /*      */
    /* opacity:0; */
  }
}
@media only screen and (min-width: 600px) {
  .hidden-sm-and-up-new {
    height:150px;
    width:100%;
  }
}
</style>



