<template>
    <!-- <v-card color="grey lighten-4" flat> -->
    <v-toolbar  fixed class="header-color">
        <v-toolbar-side-icon>
            <router-link to="/" style="color:white; text-decoration:none;">
                <v-icon>home</v-icon>
            </router-link>
        </v-toolbar-side-icon>
        <v-toolbar-title style="color:white">SSAFY 2nd Semester Team 7</v-toolbar-title>
        <v-spacer></v-spacer>

        <v-toolbar-items class="hidden-sm-and-down-new" >
            <!--  imgBannerUploader     . -->
            <imgBannerUploader></imgBannerUploader>

            <v-btn flat><router-link to="/portfolio" style="text-decoration:none;color:white">Portfolio</router-link></v-btn>
            <v-btn flat><router-link to="/post" style="text-decoration:none;color:white">Post</router-link></v-btn>
            <!--        <v-btn flat href="/login">Login</v-btn>     -->
            <login color="white"/>
            <!-- use the modal component, pass in the prop -->

            <v-btn icon @click="addBookmark" style="color:white"><v-icon>bookmark</v-icon></v-btn>
        </v-toolbar-items>

        <v-menu class="hidden-sm-and-up">
            <v-toolbar-side-icon slot="activator"><i class="fas fa-bars" style="color:white"></i></v-toolbar-side-icon>
            <v-list>

                <v-list-tile icon :to="{name: 'portfolio'}">
                    <v-list-tile-avatar>
                        <v-icon>view_list</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title>Portfolio</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <v-list-tile icon :to="{name: 'post'}">
                    <v-list-tile-avatar>
                        <v-icon>create</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title>Post</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <v-list-tile v-if="!this.user" icon :to="{name: 'login'}">
                    <v-list-tile-avatar>
                        <v-icon>person</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title>Login</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-list-tile v-if="this.user" icon :to="{name: 'login'}">
                    <v-list-tile-avatar>
                        <v-icon>person</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title>Logout</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <v-list-tile icon @click="addBookmark">
                    <v-list-tile-avatar>
                        <v-icon>bookmark</v-icon>
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title>Bookmark</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

            </v-list>
        </v-menu>

    </v-toolbar>
    <!-- </v-card> -->
</template>

<script>
import login from "./login";
import imgBannerUploader from "./imgBannerUploader";

export default {
    name: "Header",
    components:{
        login,
        imgBannerUploader
    },
    data(){
        return{
            user : false
        }
    },
    methods: {
        addBookmark() {
            const title = document.title;
            const url = location.href;
            var triggerDefault = false;
            var isChromium = window.chrome,isOpera = window.navigator.userAgent.indexOf("OPR") > -1 || window.navigator.userAgent.indexOf("Opera") > -1;

            if (window.sidebar && window.sidebar.addPanel) {
                //firefox version<23
                window.sidebar.addPanel(title, url, "");
                // console.log("firefox before 23")
            }else if(isChromium !== null && isOpera == true) {
                // Opera (chromium)
                alert("      . \n\nCtrl + D      .");
                return true;

                var elem = document.createElement("a");
                elem.setAttribute("href", url);
                elem.setAttribute("title", title);
                // elem.setAttribute("rel", "sidebar");
                elem.setAttribute("rel", "bookmark");
                elem.innerText=""
                elem.click();

            }else if ((window.sidebar && (navigator.userAgent.toLowerCase().indexOf('firefox') > -1)) || (window.opera && window.print)) {
                // Firefox version >= 23 and Opera Hotlist
                alert("      . \n\nCtrl + D      .");
                return true;

                var elem = document.createElement("a");
                elem.setAttribute("href", url);
                elem.setAttribute("title", title);
                elem.setAttribute("rel", "sidebar");
                // elem.setAttribute("rel", "bookmark");
                elem.click();
                // triggerDefault=false;
                // console.log("firefox or opera")
            } else if(window.external && ('AddFavorite' in window.external)){
                //IE
                window.external.AddFavorite(url, title);
                // console.log("IE")
            } else if (document.all) {
                //IE
                window.external.AddFavorite(url, title);
            } else {
                alert("      . \n\nCtrl + D      .");
                return true;
            }
        },
    },
    computed :{
        isLogin(){
            return this.$store.getters.getUser;
        },
    },
    watch:{
        isLogin(val, oldVal){
            if(val==''){
                this.user = false;
            }
            else {
                this.user = true;
            }
        }
    }
};
</script>

<style scoped>

@media only screen and (max-width: 600px) {
    .hidden-sm-and-down-new {
        display: none !important;
    }
}


.header-color {
    background: #b6c6cf;
}

.custom-font-size{
    font-size: 1em;
}
    </style>



