<template>
   <v-container>
      <v-responsive class="mt-5">
          <div style="float:right;">
               <v-btn flat small fab @click="toEn();">en</v-btn>
               <v-btn flat small fab @click="toKo();">ko</v-btn>
           </div>

          <v-container fill-height>
               <v-layout align-center>
                   <v-flex>
                       <div> {{title}}</div>
                       <span class="grey--text">{{created_at}}</span>
                       <v-divider class="my-3"></v-divider>
                       <!-- <v-img aspect-ratio="1.7" contain v-bind:src="img"></v-img>
                       <div class="my-3">{{body}}</div> -->

                       <!--    -->
                       <markdown-it-vue class="md-body" :content="body"/>
                   </v-flex>
               </v-layout>
           </v-container>
       </v-responsive>
   </v-container>

</template>

<script>
import 'markdown-it-vue/dist/markdown-it-vue.css'
import ImgBanner from "../components/ImgBanner";
import PortfolioList from "../components/PortfolioList";
import Post from '@/services/postController'
import { isString } from "util";
import Trans from "@/services/translate";
import MarkdownItVue from 'markdown-it-vue'

export default {
  name: "DetailPortfolio",
   components: {
       MarkdownItVue,
       ImgBanner,
       PortfolioList,
       MarkdownItVue,
   },
   data() {
       return {
           title : '',
           body : '',
           text : '',
           text1 : '',
           created_at : '',
           img : ''
       };
   },
   methods: {
       async getPortfolios() {
           try{
                this.$store.state.portfolios = await Post.getPortfolios();
            } catch(e){
                alert(e)
            }
          },
       async toKo(){
           if(this.$store.state.source == 'en'){
               return;
           }
           this.$store.commit('toKo');
           await this.setTitle();
           await this.setBody();
       },
       async toEn(){
           if(this.$store.state.source == 'ko'){
               return;
           }
           this.$store.commit('toEn');
           await this.setTitle();
           await this.setBody();
       },
       async translate(){
           try {
               this.text = await Trans.translate(this.text);
           } catch (err) {
               alert(err)
           }
       },
       async translate1(){
           try {
               console.log('t1')

              this.text1 = await Trans.translate(this.text1);
              //console.log(this.text);
          } catch (err) {
              alert(err)
          }
      },
       //data set function
       async setTitle(){
            this.text = this.title;
            console.log('settitle');
            await this.translate();
            this.title = this.text;
        },
        async setBody(){
            this.text1 = this.body;
            console.log('setbody')
            await this.translate1();
            this.body = this.text1.replace(/!\s\[\]\s/gi,"!\[\]");
            console.log(this.body)
        },
    },
    mounted : async function(){
        //  data mount
        await this.getPortfolios();
        var v = this.$store.state.portfolios[this.$route.params.id-1];
        this.title = v.portTitle;
        this.body = v.portContent;
        this.created_at = v.portDatetime;
        this.img = v.portImg;
    }
};
</script>



