<template>
  <v-layout row wrap mw-1400>
    <table style="border-right: 1px solid black;">
      <tr>
        <th></th>
        <th></th>
        <th></th>
      </tr>
      <tr v-for="log in logs">
        <td>{{log.logDatetime}}</td>
        <td>{{log.userId}}</td>
        <td>{{log.pageName}}</td>
      </tr>
    </table>
    <table>
      <tr>
        <th></th>
        <th></th>
      </tr>
      <tr v-for="log in logsCount">
        <td>{{log.pageName}}</td>
        <td>{{log.pageCount}}</td>
      </tr>
    </table>
  </v-layout>
</template>
<script>
import axios from 'axios'

export default {
  name: 'Log',
  props: {
		limits: {type: Number, default: 20},
	},
    data() {
        return {
            logs: [],
            logsCount: []
        }
    },
    async mounted() {
        await this.getLogs();
    },
    methods: {
        async getLogs() {
            axios.get("http://localhost:8080/getLog.do")
                 .then(res => {
                   this.logs = res.data.logList
                   this.logsCount = res.data.logTotalList
                 })
                 .catch(err => {
                   console.log("err : " + err)
                 })
        },
    }
}
</script>
<style>
  table{
    display: inline-block;
    margin-right: 20px;
  }
  th{
    text-align: center;
  }
  td{
    padding : 10px;
  }
</style>



